import Look from "../../components/look.vue";
export default {
  components: {
    Look
  },
  data() {
    return {
      nav: 0,
      tabList: ["工会简介", "通知公告", "工会要闻", "基层动态"],
      descList: [
        {
          title: "九江市总工会简介",
          publish: "管理员发布",
          lookNumber: "7789"
        },
        {
          title: "工会组织架构",
          publish: "管理员发布",
          lookNumber: "7789"
        }
      ],
      noticeList: [
        {
          pic: "/static/imgs/index/search.png",
          title: "九江市总工会召开新任职干部集体谈话会议",
          publish: "管理员发布",
          lookNum: "7780"
        }
      ],
      newsList: [
        {
          pic: "/static/imgs/index/search.png",
          title: "“夏送清凉”高温走访",
          publish: "管理员发布",
          lookNum: "7780"
        }
      ],
      trendsList: [
        {
          pic: "/static/imgs/index/search.png",
          title: "市总工会组织党员干部开展无偿献血活动",
          publish: "管理员发布",
          lookNum: "7780"
        }
      ],
      datatext: "录入时间",
      showLoading: false,
      tabindex: 0,
      henum: 0,
      objectArray: [],
      tip: "加载更多",
      showloadmore: false,
      PageIndex: 1,
      starobjectarrnum: 5,
      startY: 0,
	  isLoading: false,
	  pageId: "",
      currentPage: 1, // 当前页
      lastPage: 1, // 总页数
      total: 1, // 总条数
      type: 1 // 信息.企业
    };
  },
  created() {
    this.getdescListData();
    this.getnoticeListData();
    this.getnewsListData();
    this.gettrendsListData();
  },
  methods: {
    getdescListData() {
      var data = {
        page: this.currentPage,
        list_rows: 20,
        category: "信息咨询",
        channel: "工会简介"
      };
      this.$axios(this.$api.getList, data, "post")
        .then(res => {
		  console.log(res, "工会简介");
		  this.pageId = 0
          if (res.error_code == 0) {
            this.descList = res.data.data;
          }
        })
        .catch(res => {
          console.log(res, ".catch--------");
        });
    },
    getnoticeListData() {
      var data = {
        page: this.currentPage,
        list_rows: 20,
        category: "信息咨询",
        channel: "通知公告"
      };
      this.$axios(this.$api.getList, data, "post")
        .then(res => {
		  console.log(res, "通知公告");
		  this.pageId = 1
          if (res.error_code == 0) {
            this.noticeList = res.data.data;
          }
        })
        .catch(res => {
          console.log(res, ".catch--------");
        });
    },
    getnewsListData() {
      var data = {
        page: this.currentPage,
        list_rows: 20,
        category: "信息咨询",
        channel: "工会要闻"
      };
      this.$axios(this.$api.getList, data, "post")
        .then(res => {
		  console.log(res, "工会要闻");
		  this.pageId = 2
          if (res.error_code == 0) {
            this.newsList = res.data.data;
          }
        })
        .catch(res => {
          console.log(res, ".catch--------");
        });
    },
    gettrendsListData() {
      var data = {
        page: this.currentPage,
        list_rows: 20,
        category: "信息咨询",
        channel: "基层动态"
      };
      this.$axios(this.$api.getList, data, "post")
        .then(res => {
		  console.log(res, "基层动态");
		  this.pageId = 3
          if (res.error_code == 0) {
            this.trendsList = res.data.data;
          }
        })
        .catch(res => {
          console.log(res, ".catch--------");
        });
    },
    touchStart(e) {
      // 记录按下位置

      this.startY = e.targetTouches[0].pageY;
    },

    touchEnd(e) {
      // 计算移动

      if (this.isLoading) {
        return;
      }

      let endX = e.changedTouches[0].pageX,
        endY = e.changedTouches[0].pageY,
        dy = this.startY - endY;

      //判断是否向上滑动

      if (dy <= 0) {
        return false;
      }

      if (dy < 20) {
        return;
      }

      let scrollHeight = this.$el.scrollHeight;

      let scrollTop =
        document.body.scrollTop || document.documentElement.scrollTop;

      // 判断是否滑动到底部

      if (this.startY + dy >= scrollHeight) {
        //滑动距离到底部并且继续下拉

        if(this.pageId == 0) {
    		this.getdescListData();
		}else if(pageId == 1){
			this.getnoticeListData();
		} else if (pageId == 2) {
			this.getnewsListData();
		} else if (pageId == 3) {
			this.gettrendsListData();
		} else {
			this.getdescListData();
		}
      }
    },

    scrollToEnd(e) {
      // 移动执行判断

      let scrollHeight = this.$el.scrollHeight;

      let clientHeight = this.$el.clientHeight;

      let scrollTop = this.$el.scrollTop;

      if (scrollTop + clientHeight >= scrollHeight && !this.showLoading) {
        this.doLoadMore();
      }
    },
    info(id) {
      this.$router.push({
        name: "ArticleInfo",
        query: {
          id: id,
          title: this.tabList[0]
        }
      });
    }
  }
};



